import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import HowToRegRoundedIcon from '@mui/icons-material/HowToRegRounded';
import Container from 'react-bootstrap/Container';
import {NavLink} from'react-router-dom'
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Stack from '@mui/material/Stack';
import logo from '../Component/Image/EcR.png'
import 'font-awesome/css/font-awesome.min.css'
import LoginIcon from '@mui/icons-material/Login';
import TextField from '@mui/material/TextField';
import ico2 from './Image/logo.png';
import "../App.css";
import Button from '@mui/material/Button';
import InputAdornment from '@mui/material/InputAdornment';
import AccountCircle from '@mui/icons-material/AccountCircle';
import KeyIcon from '@mui/icons-material/Key';
import  {Helmet} from 'react-helmet'
import Axios from 'axios';
import {useNavigate} from 'react-router-dom'
import ScrolltoTop from './ScrolltoTop';

import { Modal, ModalFooter,Form,ModalHeader, ModalBody} from "reactstrap"

export default function Login() {
    const history = useNavigate();
    const [modal, setModal]=useState(false);
    const toggle = () => setModal(!modal);
    const [username, setuserName]=useState("");
    const [password, setPassword]=useState("");
    const[ loadstatus, setLoadStatus]=useState("");

    const login = async()=>{
       await Axios.post("http://localhost:8080/Home",{personName:username,password:password}).then((res)=>{
        //     if(res.status==200){
        //       return toggle()
        //      // alert(res.data.message);
        //     }
        //  else if(res.status==400){
        //   setLoadStatus(res.data.message)
        //  }
      
        //   else {
        //     setLoadStatus(res.data.message)
        //   }
  

        if(!res.data.message ==""){
          setLoadStatus(res.data.message);
          return  toggle()
        }
      
            })
            
    }

  return (
    <div >
 <Helmet>
            <title>ECGST - Login</title>
            <meta name="description" content="ECGST-Login" />
        </Helmet>

        <Modal isOpen={modal} toggle={toggle} modalTransition={{timeout : 200}}>
          <ModalHeader>Login</ModalHeader>
          <ModalBody>{loadstatus}</ModalBody>
          <ModalFooter>

        {loadstatus.length > 30 ? (  
          <Button   sx={{width:"50px" ,fontSize:"12px"}} color="primary" variant='contained' onClick={toggle}>Cancel</Button>):(<Button sx={{marginRight:"20px" ,fontSize:"12px"}} color="success" variant='contained' onClick={()=>{history("/home")}}>Okay</Button>)}
          </ModalFooter>
        </Modal>
    <Container fluid className='banner'>
        <Row style={{backgroundColor:"white"}}>
            <Col sm="4">
            <div className='m-2 p-1'>
            <img src={logo} width="110px" height="40px"/>
            </div>
            </Col>
            <Col  sm="4">
            <div className='m-2 p-1 PhoneTxt'>
            <i className="ace-icon fa fa-mobile" ></i><span>
           84700 74700 
           </span>
           <div className="icon">
            <i ></i>
            </div>
           </div>
            </Col>
            <Col>
 
        </Col>    
        </Row>  
    </Container>
 <div className='container'>
        
            <div className="col-sm-9 col-sm-offset-1 con">
 <div className='regiBox'>
    <Form action='/Home' method='post'>
        <fieldset>
            <div className='ecgst_title'>
ECcounting
            </div>
            
            <Row >
          <Col lg="6" className='terms_content'>
          <div class="ecgst_title_content">
          <span class="ecgst_list">
          1. Billing and Estimate <br></br>
          2. Free GST Filling <br></br>
3. Inventry
<br></br>
4. Final Accounting<br></br>
</span>	
<div className='side'>&emsp; 
<span style={{fontSize:'26px',}}>EC</span> Counting MAKES Accounting
         
<span style={{fontSize:'26px'}}> EASY</span> </div>
		</div>
          </Col>
          <Col className='col-sm-6 selt'>
            <div className='gap3'></div>
            {/* <h6 style={{color:"red"}}>{loadstatus}</h6> */}
        <TextField
        
        required
 variant='filled'
 sx={{width:"90%"}}
 type="text"
 onChange={(e)=>{setuserName(e.target.value)}}
      placeholder="Username"
        InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <AccountCircle />
              </InputAdornment>
            ),
          }}
        />
        <br/>
        <br/>
        <br/>
        <TextField
        required
        sx={{width:"90%"}}
 variant='filled'
 placeholder='Password'
 onChange={(e)=>{setPassword(e.target.value)}}
 type="password"
 InputProps={{
    startAdornment: (
      <InputAdornment position="start">
<KeyIcon/>
      </InputAdornment>
    ),
  }}
       />
       <br/>
       <br/>
       <Stack direction="row" spacing={2} sx={{marginTop:"20px"}}>
       <Button variant="contained" onClick={login} startIcon={<LoginIcon/>}>
        Login
      </Button>
      <NavLink  to="/Register" > <Button variant="contained" startIcon={<HowToRegRoundedIcon/>}>
      Register
      </Button></NavLink>
       </Stack>
       <br/>
       <div className='forget'>
      <NavLink to="/Forget">Forget Password ?</NavLink>
        </div>          </Col>
          </Row>
          
        </fieldset>

    </Form>

 </div>

 <div className='gap2'>

 </div>
            </div>
       <div className='gst-content'>
       <span>INDIA's NUMBER 1 GST SOLUTION PROVIDER. GST RETURNS WITH UNLIMITED BILL</span>
       </div>
       <div className='gap4'>
</div>

        </div>
        <div className='scroll'>
        <ScrolltoTop/>
        </div>
        <div className='footer'>
   
<p style={{padding:"11px"}}>2022  ECGST</p>
</div>
<span className='pull-right'>
  developed by{' '}
  <img src={ico2} style={{marginTop:"-10px" ,width:"20px",height:"20px"}}/>
  <span style={{color:"red" ,fontWeight:"bold"}}>Ec</span>
<span style={{color:"blue" ,fontWeight:"bold"}}>File</span>

</span>

    </div>
    
  )
}
