import React, { useEffect, useState } from "react";

import "bootstrap/dist/css/bootstrap.css"
import NotificationsIcon from '@mui/icons-material/Notifications';
import logo from '../Component/Image/EcR.png'
import HomeIcon from '@mui/icons-material/Home';
import "../App.css";
import Axios from 'axios';
import ViewSidebarIcon from '@mui/icons-material/ViewSidebar';
import DvrIcon from '@mui/icons-material/Dvr';
import DescriptionIcon from '@mui/icons-material/Description';
import ArticleIcon from '@mui/icons-material/Article';
import CreditCardIcon from '@mui/icons-material/CreditCard';
import {Helmet} from "react-helmet"
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  NavbarText
} from "reactstrap";



const Home=()=>{
const [ LoginStatus ,  setLoginStatus]=useState("")
const [ username ,  setuserName]=useState("")
const [isOpen, setIsOpen] = useState(false);

const toggle = () => setIsOpen(!isOpen);

useEffect(()=>{

   Axios.get("http://localhost:5000/login",{personName:username}).then((res)=>{
    if(res.status==200){
      alert(res.data.message);
     
    }

  else if(res.status==202){
    setLoginStatus(res.data.message)
  }


   })


},[])


  return(
    <div>
      <Helmet>
        <title>ECGST - Home</title>
       <meta name="description" content="ECGST - Home" />
      </Helmet>
<Navbar color="primary" light expand="md">

        <NavbarBrand href="/"><img src={logo} width="120px"/></NavbarBrand>
        <NavbarToggler onClick={toggle} />
   
        <Collapse isOpen={isOpen} navbar>
   
        </Collapse>
        <NavbarText >
          <NotificationsIcon sx={{width:"50px",fontSize:"30px",color:"white",marginLeft:"5px"}} />
         </NavbarText>
         <NavbarText>
          <HomeIcon   sx={{width:"50px",fontSize:"30px",color:"white",marginLeft:"5px"}}/>
         </NavbarText>
         <Nav className="mr-auto"  navbar>
      <UncontrolledDropdown nav inNavbar>
             <DropdownToggle nav caret style={{fontSize:"18px",fontWeight:"bold",color:"white", marginRight:"37px"}}>
              Welcome,{LoginStatus}
             </DropdownToggle>
             <DropdownMenu right>
               <DropdownItem>
               <NavLink href="/" >Logout</NavLink> 
                </DropdownItem>
            
              
             </DropdownMenu>
           </UncontrolledDropdown>
      </Nav>
      </Navbar>
      <Navbar color="light" light expand="md" >
          <NavbarBrand href="/"></NavbarBrand>
          <NavbarToggler  />
          <Collapse  navbar style={{fontWeight:"bold",paddingLeft:"12px"}}>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink href="/components/">CREATE <br/> FINALAC</NavLink>
              </NavItem>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                 INVOICE
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem>
                  <span> <DescriptionIcon/>{'  '}Sales Invoice</span>
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>
                  <span> <ArticleIcon/></span>   Delivery Chellan
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>
                  <span><CreditCardIcon/></span>    Credit Note(Sales<br/>Return)
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>
                  <span><DvrIcon/></span> Purchase Invoice
                  </DropdownItem>
                  <DropdownItem divider />
                  <DropdownItem>
                  <span><ViewSidebarIcon/></span>Debit Note(Purchase<br/>Return)
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
              <NavItem>
                <NavLink href="/">ESTIMATES</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/">VOUCHERS</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/">CASH/BANK</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/">PAYROLL</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="/">REPORT</NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
    </div>
  )
}
export default Home;