import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import {NavLink} from'react-router-dom'
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Stack from '@mui/material/Stack';
import logo from '../Component/Image/EcR.png'
import 'font-awesome/css/font-awesome.min.css'
import LoginIcon from '@mui/icons-material/Login';
import TextField from '@mui/material/TextField';
import ico2 from './Image/logo.png';
import "../App.css";
import Button from '@mui/material/Button';
import InputAdornment from '@mui/material/InputAdornment';
import AccountCircle from '@mui/icons-material/AccountCircle';
import {Form } from 'reactstrap'
import Axios from 'axios';
import {useNavigate} from 'react-router-dom'
import ScrolltoTop from './ScrolltoTop';
import CloseIcon from '@mui/icons-material/Close';
import { Modal, ModalFooter,ModalHeader, ModalBody} from "reactstrap"
import "../App.css";
import {Helmet} from 'react-helmet'
export default function Forget() {
    const history = useNavigate();
    const [username, setuserName]=useState("");
    const [modal, setModal]=useState(false);
    const toggle = () => setModal(!modal);
    const[ loadstatus, setLoadStatus]=useState("");
    const login = async()=>{
       await Axios.post("http://localhost:8080/forget",{personName:username}).then((res)=>{
        if(res.status==200){

          return toggle();
          // alert(res.data.message);
            // history("/backup")
        }
  
      else if(res.status==202){
        setLoadStatus(res.data.message)
      }
  
            })
            
    }

  return (
    <div >
      <Helmet>
      <title>ECGST - Forget</title>
            <meta name="description" content="ECGST-Login" />
      </Helmet>
      <Modal isOpen={modal} toggle={toggle} modalTransition={{timeout : 200}}>
          <ModalHeader>Login</ModalHeader>
          <ModalBody>User Data Found..!!!</ModalBody>
          <ModalFooter>

          <Button sx={{marginRight:"20px" ,fontSize:"12px"}} color="success" variant='contained' onClick={()=>{history("/backup")}}>Okay</Button>
          <Button   sx={{width:"50px" ,fontSize:"12px"}} color="primary" variant='contained' onClick={toggle}>Cancel</Button>
          </ModalFooter>
        </Modal>
    <Container fluid className='banner'>
        <Row style={{backgroundColor:"white"}}>
            <Col sm="4">
            <div className='m-2 p-1'>
            <img src={logo} width="110px" height="40px"/>
            </div>
            </Col>
            <Col  sm="4">
            <div className='m-2 p-1 PhoneTxt'>
            <i className="ace-icon fa fa-mobile" ></i><span>
           84700 74700 
           </span>
           <div className="icon">
            <i ></i>
            </div>
           </div>
            </Col>
            <Col>
 
        </Col>    
        </Row>  
    </Container>
 <div className='container'>
        
            <div className="col-sm-8  con">
 <div className='regiBox'>
    <Form action='/Home' method='post'>
       

            
            <Row >
         
          <Col className='col-sm-12 selt'>
            <div className='gap3'></div>
            <h6 style={{color:"red"}}>{loadstatus}</h6>
        <TextField
        
        required
 variant='filled'
 sx={{width:"90%"}}
 type="text"
 onChange={(e)=>{setuserName(e.target.value)}}
      placeholder="Username"
        InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <AccountCircle />
              </InputAdornment>
            ),
          }}
        />
      
       <br/>
       <br/>
       <Stack direction="row" spacing={2} sx={{marginTop:"20px"}}>
       <Button variant="contained" onClick={()=>{ login();}}  startIcon={<LoginIcon/>}>
        Submit
      </Button>
      <NavLink  to="/" > <Button variant="contained" startIcon={<CloseIcon/>}>
    Cancel
      </Button></NavLink>
       </Stack>
       <br/>
               </Col>
          </Row>
      

    </Form>

 </div>

 <div className='gap7'>

 </div>
            </div>
       <div className='gst-content'>
       <span>INDIA's NUMBER 1 GST SOLUTION PROVIDER. GST RETURNS WITH UNLIMITED BILL</span>
       </div>
       <div className='gap9'>
</div>

        </div>
        <div className='scroll'>
        <ScrolltoTop/>
        </div>
        <div className='footer'>
   
<p style={{padding:"11px"}}>2022  ECGST</p>
</div>
<span className='pull-right'>
  developed by{' '}
  <img src={ico2} style={{marginTop:"-10px" ,width:"20px",height:"20px"}}/>
  <span style={{color:"red" ,fontWeight:"bold"}}>Ec</span>
<span style={{color:"blue" ,fontWeight:"bold"}}>File</span>

</span>

    </div>
    
  )
}
