import React, { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Container from 'react-bootstrap/Container';
import {NavLink} from'react-router-dom'
import KeyIcon from '@mui/icons-material/Key';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Stack from '@mui/material/Stack';
import logo from '../Component/Image/EcR.png'
import 'font-awesome/css/font-awesome.min.css'
import LoginIcon from '@mui/icons-material/Login';
import TextField from '@mui/material/TextField';
import ico2 from './Image/logo.png';
import "../App.css";
import Button from '@mui/material/Button';
import InputAdornment from '@mui/material/InputAdornment';
import AccountCircle from '@mui/icons-material/AccountCircle';
import {Form } from 'reactstrap'
import Axios from 'axios';
import {useNavigate} from 'react-router-dom'
import ScrolltoTop from './ScrolltoTop';
import CloseIcon from '@mui/icons-material/Close';
import { useFormik} from'formik';
import * as yup from 'yup';
import "../App.css";
import {Helmet} from"react-helmet"
import { Modal, ModalFooter,ModalHeader, ModalBody} from "reactstrap"
const PASSWORD_REGEX = /^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/;
const ValidationSchema = yup.object({
   
   password:yup.string()
   .required('Password is required')
   .matches(PASSWORD_REGEX, "Please enter a strong password"),
  cpassword:yup.string()
  .oneOf([yup.ref('password'),null],"confirmPassword must be same with password")
  .required("Confirm password is required"),
  check:yup.string()
  .required("Terms & Condition"),
  acceptTerms: yup.bool().oneOf([true], 'Accept Terms is required'),
  
  
  })
export default function Backup() {
    const history = useNavigate();
    const[ loadstatus, setLoadStatus]=useState("");
    const [modal, setModal]=useState(false);
    const toggle = () => setModal(!modal);
 
    const login = async()=>{
       await Axios.post("http://localhost:8080/backup",{personName:formik.values.personName, password:formik.values.password,confirmPassword:formik.values.cpassword}).then((res)=>{
        // if(res.status==202){
        //   alert(res.data.message);
           
        //   // return  toggle();
        // }
//  if(res.status==200){
//     setLoadStatus(res.data.message);
//     toggle();
//   } 
//     else{
//     setLoadStatus(res.data.message);
//     }
if(!res.data.message ==""){
  setLoadStatus(res.data.message);
  return  toggle()
}
  
            })
            
    }
    const formik = useFormik({
        initialValues: {
          password:"",
          cpassword:"",
          personName:""
  
        },
       validateOnBlur: true,
  
      validationSchema: ValidationSchema,
  
  
     
    }  );
     
        
     
  
  

  return (
    <div >
         <Helmet>
      <title>ECGST - Reset</title>
            <meta name="description" content="ECGST-Login" />
      </Helmet>
      <Modal isOpen={modal} toggle={toggle} modalTransition={{timeout : 200}}>
          <ModalHeader>Login</ModalHeader>
          <ModalBody>{loadstatus}</ModalBody>
          <ModalFooter>

        {loadstatus.length > 32 ? (  
          <Button   sx={{width:"50px" ,fontSize:"12px"}} color="primary" variant='contained' onClick={toggle}>Cancel</Button>):(<Button sx={{marginRight:"20px" ,fontSize:"12px"}} color="success" variant='contained' onClick={()=>{history("/")}}>Okay</Button>)}
          </ModalFooter>
        </Modal>
     
    <Container fluid className='banner'>
        <Row style={{backgroundColor:"white"}}>
            <Col sm="4">
            <div className='m-2 p-1'>
            <img src={logo} width="110px" height="40px"/>
            </div>
            </Col>
            <Col  sm="4">
            <div className='m-2 p-1 PhoneTxt'>
            <i className="ace-icon fa fa-mobile" ></i><span>
           84700 74700 
           </span>
           <div className="icon">
            <i ></i>
            </div>
           </div>
            </Col>
            <Col>
 
        </Col>    
        </Row>  
    </Container>
 <div className='container'>
        
            <div className="col-sm-8  con">
 <div className='regiBox'>
    <Form onSubmit={formik.handleSubmit} method='post'>
       

            
            <Row >
         
          <Col className='col-sm-12 selt'>
            <div className='gap3'></div>
            {/* <h6 style={{color:"red"}}>{loadstatus}</h6> */}

            <TextField
        name='personName'
        required
 variant='filled'
 sx={{width:"90%"}}
 type="text"
 onChange={formik.handleChange}
 value={formik.values.personName}
      placeholder="Username"
        InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <AccountCircle />
              </InputAdornment>
            ),
          }}
        />
        <br/>
        <br/>
        <TextField
        
        required
        name='password'
 variant='filled'
 sx={{width:"90%"}}
 type="password"
 onChange={formik.handleChange}
 value={formik.values.password}
      placeholder="password"
        InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <AccountCircle />
              </InputAdornment>
            ),
          }}
        />
      {formik.errors.password ? <div className='text-danger error'>{formik.errors.password}</div> : null}
       <br/>
       <br/>
      
        <TextField
        required
        name='cpassword'
        sx={{width:"90%"}}
 variant='filled'
 placeholder='ConfirmPassword'
 onChange={formik.handleChange}
 value={formik.values.cpassword}
 type="password"
 InputProps={{
    startAdornment: (
      <InputAdornment position="start">
<KeyIcon/>
      </InputAdornment>
    ),
  }}
       />
       {formik.errors.cpassword ? <div className='text-danger error'>{formik.errors.cpassword}</div> : null}
       <Stack direction="row" spacing={2} sx={{marginTop:"20px"}}>
       <Button variant="contained" onClick={()=>{ login();}}  startIcon={<LoginIcon/>}>
        Submit
      </Button>
      <NavLink  to="/" > <Button variant="contained" startIcon={<CloseIcon/>}>
    Cancel
      </Button></NavLink>
       </Stack>
       <br/>
               </Col>
          </Row>
      

    </Form>

 </div>


            </div>
       <div className='gst-content'>
       <span>INDIA's NUMBER 1 GST SOLUTION PROVIDER. GST RETURNS WITH UNLIMITED BILL</span>
       </div>
       <div className='gap10'>
</div>

        </div>
        <div className='scroll'>
        <ScrolltoTop/>
        </div>
        <div className='footer'>
   
<p style={{padding:"11px"}}>2022  ECGST</p>
</div>
<span className='pull-right'>
  developed by{' '}
  <img src={ico2} style={{marginTop:"-10px" ,width:"20px",height:"20px"}}/>
  <span style={{color:"red" ,fontWeight:"bold"}}>Ec</span>
<span style={{color:"blue" ,fontWeight:"bold"}}>File</span>

</span>

    </div>
    
  )
}
